      Potentialen fr manipulerade videor r enorma, inte minst fr filmindustrin dr ansikten p stuntmn och -kvinnor kan ersttas med knda skdespelare, eller dr tidskrvande omtagningar kan ersttas med datorgenererat material. Men riskerna fr missbruk r uppenbara, och en av de drivande krafterna just nu kommer ifrn privatpersoner som skapar fejkad kndisporr. Fria algoritmer, ssom <a href="https://computersweden.idg.se/2.2683/1.696965/deepfakes-riskerna-med-ai" title="Computer Sweden: Deepfakes r det lskigaste p ntet just nu" target="_blank">Deepfake</a>, frpackade i anvndarvnliga program gr det frhllandevis enkelt fr vem som helst att, med en bra hemdator och datorvana, skapa och sprida fejkat videomaterial.
    </p>

    <p>
      Algoritmerna, som blir alltmer avancerade, gr i regel ut p att lra programmet vilka egenskaper som personens rst och ansikte har. Detta grs genom att trna AI:n med tidigare inspelat ljud- och videomaterial. Denna process gr att gra p en bra hemdator och tar idag alltifrn ett par timmar och uppt, ifall bra resultat ska erhllas. Det enda som behvs r ngra hundra bilder av en person frn olika vinklar och med varierande ansiktsuttryck, eller ngra kortare videoklipp. Det r sdant som de flesta idag delar utan att tnka, p exempelvis sociala medier.
    </p>

    <p>
      I gif-bilden nedan visas ett eget test med programmet <a href="https://computersweden.idg.se/2.2683/1.696965/deepfakes-riskerna-med-ai" title="Computer Sweden: Deepfakes r det lskigaste p ntet just nu" target="_blank">FakeApp</a> i vilken Deepfakes AI-algoritmer lr sig egenskaperna fr mitt och Donald Trumps ansikte, samt hur dessa kan erstta varandra i bilder och videoklipp. Ju lngre tid programmet fr trna, desto bttre blir de resulterande fejkvideorna.
    </p>

    <media-container class="mediaContainer">
  		<div>
        <div class="content">
    		    <iframe src="https://giphy.com/embed/3ohs4wXgEsLNcHVC9i" width="600" height="350" frameBorder="0" class="giphy-embed" allowFullScreen></iframe>
        </div>
    		<div class="caption">
          Deepfakes AI-algoritmer lr sig egenskaperna fr mitt och Donald Trumps ansikte, samt hur dessa kan erstta varandra i bilder och videoklipp.
    		</div>
        <div class="source">
          Klla: <a href="https://giphy.com/gifs/training-ai-deepfake-3ohs4wXgEsLNcHVC9i" title="Giphy: Deepfake training" target="_blank">Mikoteket (Giphy)</a>
    		</div>
  		</div>
  	</media-container>

    <p>
      Utifrn trningen ovan har ett frsta frsk gjorts till att skapa en frdig deepfake. I videon, som <a href="https://www.youtube.com/watch?v=xjHlrHZD3DY" title="Donald Trump: I'm running for President" target="_blank">i sitt original</a> visar Trumps tal som blivande presidentkandidat, har mitt eget ansikte ersatt Trumps och fljer dennes rrelser och uttryck. Resultatet r bitvis trovrdigt men kvaliteten verlag har brister, mycket p grund av fljande anledningar:
    </p>

    <ol>
      <li>
        Bristande kllmaterial vid AI-trning:
        <ul>
          <li>
            Fr f (drygt 100) bilder har anvnts fr respektive ansikte.
          </li>
          <li>
            Fr f vinklar och ansiktsuttryck har anvnts.
          </li>
        </ul>
      </li>
      <li>
        Fr kort AI-trning: ca 8h.
      </li>
      <li>
        Felaktiga parameterinstllningar vid slutgiltig mixning.
      </li>
    </ol>

    <media-container class="mediaContainer">
      <div>
        <div class="content">
            <iframe src="https://player.vimeo.com/video/254463729" width="640" height="360" frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>
        </div>
        <div class="caption">
          Mitt ansikte erstter Donald Trumps i ett av hans tal som presidentkandidat. Resultatet r stundvis trovrdigt men kvaliteten verlag har brister, delvis p grund av bristfllig trning av AI:n.
        </div>
        <div class="source">
          Klla: <a href="https://vimeo.com/254463729" title="Vimeo: Martin Trump as President!" target="_blank">Mikoteket (Vimeo)</a>
        </div>
      </div>
    </media-container>

    <p>
      Mjligheterna till att skapa trovrdigt fejkat videomaterial, och framfrallt enkelheten i att framstlla deepfakes, r imponerande. Men riskerna fr trovrdigt fejkad <a-wiki c="Hmndporr">hmndporr</a-wiki>, d.v.s. bild- eller videomaterial p annan person som sprids i hmndsyfte, och annat utpressnings- och propagandamaterial r verhngande. Inte minst med tanke p de drivande krafterna fr utvecklingen av de bakomliggande algoritmerna.
    </p>

    <info-fields class="infoFields">

      <div class="summary">

        <div class="hBox">
          SAMMANFATTNING
        </div>

        <p>
          Med den snabba utvecklingen av artificiell intelligens och hrdvara r det mjligt att skapa trovrdigt manipulerat ljud- och videomaterial. Riskerna att tekniken missbrukas fr exempelvis hmndporr, utpressning och propagandaspridning r verhngande.
        </p>

      </div>

      <div class="more">

        <div class="hBox">
          LS MER
        </div>

        Computer Sweden skriver om <a href="https://computersweden.idg.se/2.2683/1.696965/deepfakes-riskerna-med-ai" title="Computer Sweden: Deepfakes r det lskigaste p ntet just nu  och ett tydligt exempel p riskerna med AI
" target="_blank">deepfakes och riskerna med AI</a> samt presenterar trovrdiga exempel skapade av Reddit-anvndare.

      </div>

    </info-fields>

  </div>
</template>

