		<p>
			Problemet r just att vi har ett <i>verfld</i> av information som r omjligt att verblicka och navigera i manuellt, och vi har drfr behov av filter och <a href="https://sv.wikipedia.org/wiki/Algoritm" title="Wikipedia: Algoritm" target="_blank">algoritmer</a> som hanterar informationsfldet t oss. I grund och botten r de allra flesta sdana algoritmer designade fr att ge dig som anvndare en s smidig och trevlig upplevelse som mjligt, och r anpassade fr att ge personliga resultat. Ett exempel p tjnster som anvnder sdana algoritmer r <a href="https://sv.wikipedia.org/wiki/S%C3%B6ktj%C3%A4nst" title="Wikipedia: Sktjnst" target="_blank">skmotorer</a> som ger resultat baserat p din geografiska position, din och andra anvndares internethistorik och andra personliga parametrar.
		</p>

