<template>
  <div>

    <cover-image header="Instllningar fr tillgnglighet"></cover-image>

    <div class="container">

      <div class="containerInner">

        <div class="columnLeft">
        </div>

        <div class="columnCenter">

          <p>
            <a @click="back()">G tillbaka till fregende sida</a>
          </p>

          <h3>Mrk bakgrund</h3>
          <p>
            Ljus text p mrk bakgrund kan upplevas som mindre pfrestande fr gonen.
          </p>
          <label class="semibold">
            <input type="checkbox" v-model="accessibility.contrast" />
            Aktivera mrk bakgrund
          </label>

          <h3>Nedtoning</h3>
          <p>
            Mrka ner skrmen och behll en ljus strimma som fljer musen. Verktyget kan hjlpa att fokusera vid lsning av lngre textavsnitt.
          </p>
          <label class="semibold">
            <input type="checkbox" v-model="accessibility.ruler" />
            Aktivera nedtoning
          </label>

          <h3>Typsnitt fr dyslexi</h3>
          <p>
            Typsnittet <a href="https://www.opendyslexic.org/" target="_blank">OpenDyslexic</a> r designat fr kad lsbarhet i syfte att motverka vanliga symptom relaterade till dyslexi.
          </p>
          <label class="semibold">
            <input type="checkbox" v-model="accessibility.dyslexia" />
            Aktivera typsnitt fr dyslexi
          </label>

        </div>

        <div class="columnRight">
        </div>

      </div>

    </div>

  </div>
</template>

<script>

import { store } from '@/store';

import CoverImage from '@/components/CoverImage.vue'

export default {
  components: {
    CoverImage,
  },
  props: {
  },
  computed: {
    accessibility: function () {
      return store.state.accessibility
    },
  },
  methods: {
    toggleAccessibility: function (name) {
      store.commit('toggleAccessibility',name);
    },
    back: function () {
      window.history.back();
    }
  }
}
</script>

<style scoped>

</style>



