<template>
  <div>

    <p>
      En ndvndighet fr att kunna skapa ett tillgngligt samhlle, med tillgngliga digitala och analoga verktyg och tjnster, r att ha frstelse fr olika personers olika frutsttningar. ven nr vi vet vad som r bst fr <i>oss sjlva</i>, r det desto svrare att frst <i>andra personers</i> upplevelse och intryck. Fr personer <i>utan</i> funktionsnedsttning r det exempelvis vldigt svrt att frst hur olika funktionsvariationer pverkar olika individer.
    </p>

    <p>
      Digital teknik kan hjlpa personer att f inblick i hur olika funktionsvariationer kan upplevas. Det bidrar till kad frstelse i syfte att skapa tillgngliga miljer, verktyg och tjnster.
    </p>

    <h3>Simuleringar</h3>

    <p>
      Med digitala verktyg r det mjligt att <a-wiki c="Simulering">simulera</a-wiki> frekommande symptom av olika funktionsnedsttningar. De tre Ishihara-bilderna nedan r <a href="http://www.rgblind.se/assets/images/Ishihara_9.png" target="_blank">samma digitala bild</a>, men medan den vnstra bilden visas som vanligt har bilderna i mitten och till hger applicerade filter som simulerar protanopi och deuteranopi, tv vanligt frekommande typer av <a-wiki c="Defekt_frgseende">frgblindhet</a-wiki>. Medan en person med fullgott frgseende ser talet 74 ser personer med protanopi oftast inget mnster alls, och personer med deuteranopi urskiljer ibland talet 21.
    </p>

    <div class="ishihara">
      <div>
        <img src="http://www.rgblind.se/assets/images/Ishihara_9.png" alt="Ishihara testbild fr frgblindhet, frestllande numret 74.">
      </div>
      <div>
        <img src="http://www.rgblind.se/assets/images/Ishihara_9.png" alt="Ishihara testbild fr frgblindhet, frestllande numret 74, med filter fr simulerad protanopi." class="protanopia"/>
      </div>
      <div>
        <img src="http://www.rgblind.se/assets/images/Ishihara_9.png" alt="Ishihara testbild fr frgblindhet, frestllande numret 74, med filter fr simulerad deuteranopi." class="deuteranopia"/>
      </div>
    </div>

    <p>
      I graferna nedan urskiljer personer med fullgott frgseende enkelt staplarna och dess frger, svl i den vre som i den undre grafen. Genom att simulera protanopi framgr det dock tydligt att den vre grafens frgval kan innebra stora svrigheter fr personer med olika typer av frgblindhet, och att frgvalen i den undre grafen r att fredra. Utan simuleringsverktyg kan detta vara oerhrt svrt fr en person med fullgott frgseende att frst, d frgskalorna kan tyckas snarlika.
    </p>

    <div :class="{'protanopia': color == 'protanopia'}">

      <div class="center">
        Simulera frgseende:
        <button @click="color = 'normal'" :class="{green: color == 'normal'}">Normalt frgseende</button>
        <button @click="color = 'protanopia'" :class="{green: color == 'protanopia'}">Protanopi</button>
      </div>

      <br />

      <iframe width="100%" height="320" frameborder="0" scrolling="no" src="//plot.ly/~interaktivarum/3.embed" class="border"></iframe>
      <iframe width="100%" height="320" frameborder="0" scrolling="no" src="//plot.ly/~interaktivarum/1.embed" class="border"></iframe>

      <br />

      <div class="center">
        Simulera frgseende:
        <button @click="color = 'normal'" :class="{green: color == 'normal'}">Normalt frgseende</button>
        <button @click="color = 'protanopia'" :class="{green: color == 'protanopia'}">Protanopi</button>
      </div>

    </div>

    <p>
      I exemplena ovan anvnds simuleringsverktyget <a href="http://www.rgblind.se" target="_blank">RGBlind</a> (utvecklat av Interaktiva rum som driver Mikoteket) som r skapat i syfte att hjlpa organisationer, designers och webbutvecklare att f inblick i hur frgblinda anvndare kan uppleva deras hemsidor, frst problematiken med att designa och utveckla fr frgblinda anvndare, samt att bidra till utvecklingen av strukturerade webbsidor med hg lsbarhet fr fler.
    </p>

    <p>
      Svenskutvecklade <a href="https://www.funkify.org/" target="_blank">Funkify</a> r ett webblsartillgg som simulerar symptom av bland annat nedsatt syn, motoriska nedsttningar, koncentrationssvrigheter och dyslexi, och lter anvndaren f en frstelse fr hur dessa pverkar upplevelsen av att surfa p webben. P Webbriktlinjer listas <a href="https://webbriktlinjer.se/simulera" target="_blank">fler anvndbara simuleringsverktyg</a>.
    </p>

    <p>
      Det r ven mjligt att gra enkla analoga simuleringar av olika funktionsnedsttningar. En par tejpbitar p ett par oslipade
    </p>

    <info-fields class="infoFields">

      <div class="excercise">

        <div class="hBox">
          Simulera funktionsnedsttningar: analogt
        </div>

        <p>
          Med fljande enkla medel, testa att uppleva vardagen med olika funktionsnedsttningar:
        </p>

          <ul>
            <li>
              <span class="semibold">Nedsatt syn</span>: fst ett par halvgenomskinliga tejpbitar p ett par oslipade glasgon. Ju dimmigare tejp, desto grvre synfel. Anvnd svart frg fr att simulera exempelvis tunnelseende.
            </li>
            <li>
              <span class="semibold">Nedsatt hrsel</span>: Anvnd ett par bekvma hrselkpor.
            </li>
            <li>
              <span class="semibold">Nedsatt knsel</span>: Anvnd <a href="https://www.google.se/search?q=touchvantar&tbm=shop" target="_blank">touchvantar</a> nr du anvnder din telefon eller annan touchenhet.
            </li>
            <li>
              <span class="semibold">Muskelryckningar</span>: Medan du anvnder musen och tangentbordet, lt en vn rycka i din verarm.
            </li>

          </ul>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Hur pverkar funktionsnedsttningen mjligheten att utfra olika uppgifter eller delta i olika sociala situationer?
          </li>
          <li>
            Utifrn olika situationer och fr respektive funktionsnedsttning, vilka vriga sinnen och intryck blir desto viktigare?
          </li>
          <li>
            Hur kan liknande vningar designas fr att simulera andra funktionsnedsttningar?
          </li>
        </ul>

      </div>

      <div class="excercise">

        <div class="hBox">
          Simulera funktionsnedsttningar: digitalt
        </div>

        <p>
          Fr <a href="https://www.google.se/chrome/" target="_blank">Chrome</a>-anvndare: <a href="https://chrome.google.com/webstore/detail/funkify-disability-simula/ojcijjdchelkddboickefhnbdpeajdjg" target="_blank">Installera webblsartillgget Funkify</a> och prova ngra av verktygets simuleringar av funktionsnedsttningar.
        </p>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Hur pverkar funktionsnedsttningen lsabarheten och mjligheten att navigera p webbplatsen?
          </li>
          <li>
            Hur kan digitala verktyg hjlpa skapa inblick och frstelse fr olika funktionsnedsttningar?
          </li>
          <li>
            Hur kan liknande verktyg designas fr att simulera andra funktionsnedsttningar?
          </li>
          <li>
            Hur kan liknande verktyg designas fr att simulera funktionsnedsttningar utanfr webben?
          </li>
        </ul>

      </div>


    </info-fields>

    <h3>VR-upplevelser</h3>

    <p>
      Med 360-bilder och -videor r det mjligt att skapa immersiva upplevelser med <a-wiki c="VR-glasgon">VR-glasgon</a-wiki>. Bilderna och filmerna r skapade s att anvndaren kan vrida sig sjlv, den mobila skrmen, eller sjlva innehllet fr att p s stt kunna se sig om i alla riktningar. Upplevelsen blir starkast med VR-glasgon, men ofta gr det att uppleva bilderna och filmerna ven p en vanlig skrm.
    </p>

    <p>
      I <a href="http://www.mfd.se" target="_blank">Myndigheten fr delaktighets</a> verktyg <a href="http://www.mfd.se/stod-och-verktyg/digitala-verktyg/simulatorer-och-kostnadssnurror/funktionssimulatorn/" target="_blank">Funktionssimulatorn</a> r det mjligt att uppleva och jmfra olika sociala situationer, <i>utan</i> respektive <i>med</i> kognitiv funktionsnedsttning. I ett klassrum kan exempelvis ljud frn klasskompisarna, frgglada lappar och affischer som kommer till liv fnga ens uppmrksamhet, ngot som gr det svrt att koncentrera sig p den presenterade uppgiften.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="content">
          <iframe src="https://www.youtube.com/embed/a7U-A6evmHo" width="100%" height="360" frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>
        </div>
        <div class="caption">
          Funktionssimulatorn lter dig uppleva klassrumsundervisning med simulerad kognitiv funktionsnedsttning.
        </div>
        <div class="source">
          Klla: <a href="https://www.youtube.com/watch?v=a7U-A6evmHo" title="Youtube: Delta i klassundervisning (med kognitiv funktionsnedsttning)" target="_blank">mfdse (Youtube)</a>
        </div>
      </div>
    </media-container>

    <info-fields class="infoFields">

      <div class="excercise">

        <div class="hBox">
          Simulera koncentrationssvrigheter
        </div>

        <p>
          Jmfr <a href="http://www.mfd.se/stod-och-verktyg/digitala-verktyg/simulatorer-och-kostnadssnurror/funktionssimulatorn/" target="_blank">Funktionssimulatorns</a> olika videor <i>utan</i> och <i>med</i> simulerad funktionsnedsttning. Fr bsta effekt, anvnd ett par VR-glasgon, exempelvis <a href="https://www.google.se/search?q=vr+cardboard&tbm=shop" target="_blank">billiga alternativ av kartong</a> och hrlurar.
        </p>

        <Combat></Combat>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Hur pverkar koncentrationssvrigheter frmgan att ta in information?
          </li>
          <li>
            Upplever du videorna kar din frstelse fr hur koncentrationssvrigheter kan pverka vardagen?
          </li>
          <li>
            Vilka freml, ljud, etc. finns i det rum som du befinner dig just nu, som kan frsvra koncentrationen?
          </li>
          <li>
            Vad skulle kunna hnda i en liknande video, skapad fr det rum du befinner dig i just nu?
          </li>
          <li>
            Hur kan det rum du befinner dig i gras mer tillgngligt fr personer med olika funktionsnedsttningar?
          </li>
        </ul>

      </div>

    </info-fields>

    <h3>Diskriminerande design och teknik</h3>

    <p>
      Vad hnder ifall designers och utvecklare inte utgr ifrn anvndarens behov, ifall frstelse saknas fr <i>vem</i> som kommer att anvnda ett verktyg eller en tjnst, och <i>hur</i>? I bsta fall kommer designen att fungera fr samtliga anvndare, men i vrsta fall kan tekniken vara helt obrukbar fr vissa personer och upplevas som diskriminerande.
    </p>

    <p>
      Till skillnad mot <a-wiki c="Exkluderande_design">exkluderande design</a-wiki>, som innebr <i>medveten</i> utformning fr att frhindra viss anvndning, syftar diskriminerande design hr p <i>omedvetna</i> designbeslut som resulterar i frsmrad eller begrnsad anvndning fr vissa grupper av anvndare. Det kan till exempel handla om taligenknning som krver att anvndaren artikulerar klart och tydligt och som drfr <a href="https://slate.com/technology/2018/10/voice-assistants-alexa-siri-speech-disabilities-recognition.html" target="_blank">fungerar dligt fr personer med talsvrigheter</a> eller som <a href="https://www.youtube.com/watch?v=CYvFxs32zvQ" target="_blank">inte frstr accenter</a> och kraftiga brytningar, eller ldre versioner av <a href="https://techworld.idg.se/2.2524/1.709621/recaptcha-v3-google" target="_blank">reCAPTCHA</a> som krver att anvndaren bevisar att hen inte r en bot genom att identifiera svrtolkad text eller bilder - en uppgift som kan upplevas som svr oavsett funktionsfrmga.
    </p>

    <p>
      I fregende kapitel nmndes den interaktiva installationen Dansa med synapser som anvnder en <a-wiki c="Kinect">Kinectkamera</a-wiki> fr att berkna personers position i rummet. Fr tidiga versioner av Kinect och liknande kameror var anvndaren tvungen att frst posera i givna stllningar och frutsatte att anvndaren stod upp fr att tekniken skulle fungera. Detta innebar som fljd att personer i rullstol eller mnga rrelsehindrade personer ej kunde anvnda tekniken.
    </p>

    <p>
      I mycket av dagens digitala teknik finns olika typer av <a-wiki c="Sensor">sensorer</a-wiki> som mter olika typer av data, fr att exempelvis kra viss programkod eller utfra en uppgift vid srskild <a-wiki c="Stimuli">stimuli</a-wiki>. Fr designers och utvecklare r det viktigt att veta hur sensorerna fungerar fr att skerstlla att produktens eller tjnstens funktion r tillgnglig fr samtliga anvndare. I Twitterinlgget nedan visas en video med en tvlmatare som endast reagerar p ljusa hudtoner, med fljd att en mrkhyad person behver anvnda en vit pappershandduk fr att f maskinen att mata ut tvl. Detta r sannolikt ett omedvetet misstag frn fretaget som tillverkar tvlmataren, men kan vara ett tecken p bristande mngfald inom fretaget: det har uppenbarligen inte resonerats kring eller tagits hnsyn till det faktum att olika personer har olika hudton. Upplevelsen fr anvndaren r katastrofal och situationen kan upplevas som rasistisk.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="content">
          <embed-twitter href="https://twitter.com/nke_ise/status/897756900753891328"></embed-twitter>
        </div>
        <div class="caption">
          Den automatiska tvlmataren reagerar inte p mrka hudtoner, och fungerar endast fr personer fr ljus hy.
        </div>
        <div class="source">
          Klla: <a href="https://twitter.com/nke_ise/status/897756900753891328" title="@nke_ise (Twitter)" target="_blank">@nke_ise (Twitter)</a>
        </div>
      </div>
    </media-container>

    <p>
      Exemplet ovan r sannolikt en fljd av ett felaktigt instllt <a-wiki c="Trskeleffekt">trskelvrde</a-wiki> fr tvlmatarens <a-wiki c="Infrard_strlning">IR</a-wiki>-sensor. Problematiken r frhllandevis enkel att svl synliggra som fr det bakomliggande fretaget att tgrda. Andra program och verktyg, som r utvecklade med <a-wiki c="Artificiell_intelligens">artificiell intelligens</a-wiki> (AI), r desto svrare fr en mnniska att verblicka, och erhllna resultat kan vara svra att frst. Istllet fr att vara explicit programmerade (nr tillrckligt mycket ljus reflekteras in i sensorn: mata ut tvl) r en AI programmerad fr att ta egna intelligenta beslut utifrn given <a-wiki c="Datautvinning">trningsdata</a-wiki> och ramar. Det innebr mer flexibilitet, men frutstter ocks att trningsdatan r av hg kvalitet. Att trna en AI r (extremt frenklat) lite som <a-wiki c="Hundtrning">hundtrning</a-wiki>: en hund har frutsttningen att lra sig mycket, men det r upp till husse och matte att utfra lmpliga vningar och att flja vissa principer, belna nskat beteende, och ignorera eller korrigera onskat beteende.
    </p>

    <p>
      I Twitterinlgget nedan visas ett resultat av Google Photos AI, dr algoritmerna har tilldelat tv personer etiketten <i>Gorillas</i>. Det r, som beskrivet ovan, svrt att frst de exakta sklen till den felaktiga etiketten, men ifall anvndaren upplever att den beror p hudfrg eller ansiktsdrag kan tekniken ven i detta fall upplevas som rasistisk.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="content">
          <embed-twitter href="https://twitter.com/jackyalcine/status/615329515909156865"></embed-twitter>
        </div>
        <div class="caption">
          Google Photos AI har tilldelat tv personer etiketten <i>Gorillas</i>.
        </div>
        <div class="source">
          Klla: <a href="https://twitter.com/jackyalcine/status/615329515909156865" title="@jackyalcine (Twitter)" target="_blank">@jackyalcine (Twitter)</a>
        </div>
      </div>
    </media-container>

    <p>
      Det grs idag mycket forskning kring algoritmer och etik, och kring problematiken med att AI riskerar att spegla vra frdomar och sterotypa frestllningar. Det finns stora frhoppningar om att <a href="https://www.nyteknik.se/opinion/algoritmer-utan-etik-ger-ai-utan-moral-6902790" target="_blank">algoritmer skrivna med etiska principer</a> i stllet kan styra utvecklingen mot mngfald och lngsiktig hllbarhet.
    </p>

    <p>
      En frutsttning fr tillgnglig teknik r att svl designers som utvecklare har en grundlggande frstelse fr hur den produkt eller tjnst de utformar, samt de tekniska komponenterna som ingr, faktiskt fungerar, och <i>hur</i> och av <i>vem</i> den kommer att anvndas.
    </p>

    <p>
      Det kan vara svrt att skapa design som fungerar perfekt fr samtliga anvndare, men (terigen) enligt designprincipen <a-wiki c="Design_fr_alla">Design fr alla</a-wiki> s innebr ett samhlle eller en produkt/tjnst designad utan hinder fr de personer med strst behov ofta frdelar fr samtliga anvndare. I de fall det inte r praktiskt mjligt, och dr grunddesignen begrnsar anvndningen fr olika personer, br alternativa stt att anvnda produkten eller tjnsten tillhandahllas.
    </p>

  </div>
</template>

<script>

import AWiki from '@/components/AWiki.vue'
