import SearchComponent from '@/components/learn/SearchComponent.vue'
import SoiGraph from '@/components/SoiGraph.vue'
import SlideComponent from '@/components/learn/SlideComponent.vue'
import EmbedTwitter from '@/components/embed/EmbedTwitter.vue'

export default {
  components: {
    AWiki,
    InfoFields,
    MediaContainer,
    SearchComponent,
    SoiGraph,
    SlideComponent,
    EmbedTwitter
  },
  data () {
    return {
      'header': 'Inblick och frstelse',
      color: "normal"
    }
  },
  created: function () {
    let scr = document.createElement('script')
    scr.setAttribute('src', 'http://www.rgblind.se/script/rgblind/rgblind.js')
    document.head.appendChild(scr)
  }
}
</script>

<style scoped>

.ishihara{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.ishihara > div{
  flex: 1;
}

