      <span class="semibold">Kommentar till underskningarna:</span> Mycket av datan, ssom den presenteras i respektive undersknings webbverktyg, r <i>inte direkt jmfrbar</i> och slutsatser br dras med frsiktighet. Som exempel presenterar SOI ofta sin data med <a href="https://2018.svenskarnaochinternet.se/allmant-om-internetutvecklingen/nastan-alla-anvander-internet-upp-till-65-ars-alder/" target="_blank">svarsfrdelning per ldersgrupp</a> medan SFOI istllet visar <a href="http://internet.begripsam.se/#/stats/Anva%25CC%2588ndning%2520av%2520internet%2520SMFOI%25202017/b/2051" target="_blank">ldersfrdelning per svarsalternativ</a>. En frhastad slutsats vore att felaktigt hrleda att 43% av 31-54-ringarna med funktionsnedsttning inte anvnder internet.
