    <p>
			I detta spr diskuteras digital teknik ur ett tillgnglighetsperspektiv. Vad innebr design, och vad avgr om en viss design r bra eller dlig? Hur kan digital teknik anvndas som std i vardagen fr personer med olika funktionsvariationer och behov, eller hjlpa var och en att bttre frst andra personers vardag? Och vad innebr diskriminerande teknik och algoritmer?
		</p>

    <p>
      Innehllet fokuserar huvudsakligen p digital tillgnglighet fr anvndare med olika typer av funktionsnedsttning. En funktionsnedsttning innebr att en person, i relation till samhllets normer, har <a href="https://www.1177.se/Stockholm/Fakta-och-rad/Sjukdomar/Funktionsnedsattning/" target="_blank">nedsatt frmga att fungera fysiskt, psykiskt eller intellektuellt</a>. Nedsttningen kan vara i olika grad och pverka livet olika mycket, och vara medfdd eller uppkomma efter till exempel en sjukdom eller en olycka. En funktionnesttning kan vara fysisk, neuropsykiatrisk eller kognitiv, och kan vara tillfllig eller vara hela livet.
    </p>

