<template>
  <div>

    <slide-component>
      <ul>
				<li>De versta resultaten r inte alltid de bsta</li>
				<li>Jmfr olika resultat, granska kllorna</li>
				<li>Verktyg fr att filtrera resultaten</li>
				<li>Upp till dig att omvandla informationen till egen kunskap</li>
			</ul>
    </slide-component>

		<p>
			Nr du sker p webben s gr skmotorn en gissning av vad du letar efter och presenterar resultaten i en lista. Det r ltt att tro att de versta resultaten alltid r de bsta, men informationen du sker kan mycket vl finnas lngre ner i resultatlistan. Ibland r det ndvndigt att gra en personlig sammanvgning av flera olika skresultat fr att sjlv komma fram till ett godtagbart svar.
		</p>

		<p>
			Ifall du sker efter objektiv fakta, e.g. <i>vilket r Frankrikes statsskick?</i>, r det sannolikt att det korrekta svaret framgr hgt upp i resultatlistan; medan subjektiva skningar, e.g. <i>vilket r det bsta statsskicket?</i> krver att du stller dig betydligt mer kritisk till skresultaten. Nj dig inte med att godta de versta resultaten, utan granska och jmfr fler olika resultat och dess kllor.
		</p>

		<p>
			Skmotorerna erbjuder ofta verktyg eller avancerade skfunktioner fr att lta dig filtrera resultaten, exempelvis p datum, sprk och region.
		</p>

		<p>
			Kom ihg: skmotorerna r designade fr att hjlpa dig, som anvndare, att navigera p internet. De r inte allvetande orakel, utan fljer algoritmer som r programmerade av mnniskor. Det r upp till dig sjlv att gra det slutgiltiga kllkritiska arbetet, och att omvandla informationen till egen kunskap.
		</p>

		<info-fields class="infoFields">
			<div class="excercise">

				<div class="hBox">
					TESTA SJLV: JMFR OCH GRANSKA SKRESULTATEN
				</div>

				<p>
					Anvnd skflten nedan fr att ska efter objektiv fakta och subjektiva svar.
				</p>

				<search-component query="vilket r frankrikes statsskick?"></search-component>
				<search-component query="vilket r den bsta religionen?"></search-component>
				<search-component query="hur mnga i vrlden har tillgng till internet?"></search-component>

				<div class="hBox">
					DISKUTERA
				</div>

				<ul>
					<li>Hur skiljer sig resultaten t mellan de olika skningarna?</li>
					<li>r svaren sakliga eller personliga sikter? Vem r avsndaren? Granska kllan!</li>
					<li>Anser du att de versta resultaten r de bsta?</li>
					<li>Nr och varfr behver vi vara kritiska till skmotorernas resultat?</li>
				</ul>

			</div>

			<div class="summary">

				<div class="hBox">
					SAMMANFATTNING
				</div>

				Objektiva skningar ger ofta bra resultat, medan subjektiva skningar krver att du kritiskt granskar resultaten. Det r inte skert att de versta skresultaten ger de bsta svaren.

			</div>

		</info-fields>

		<h3>Organiska skresultat och skoptimering</h3>

    <slide-component>
      <ul>
				<li>Grundprincip: resultaten sorteras efter bedmd relevans</li>
				<li>Gr ej att kpa bttre placering</li>
				<li>Skoptimering: relevant information, nyckelord, m.m.</li>
			</ul>
    </slide-component>

		<p>
			Du har tidigare ftt lra dig hur skmotorers algoritmer arbetar fr att presentera de mest relevanta svaren fr din skning. Sdana resultat kallas fr <a href="https://support.google.com/adwords/answer/6054492" title="Google: Organiska skresultat" target="_blank">organiska skresultat</a> och utgr frn en mngd webbplats- och anvndarspecifika parametrar. Grundprincipen fr de organiska resultaten r att de ska ha s hg relevans som mjligt och att ordningen inte ska g att pverka genom att kpa sig till en bttre placering.
		</p>

		<p>
			Fr fretag, och andra som vill synas p internet, r det vrdefullt att hamna hgt upp bland de organiska skresultaten. Detta kar avsevrt chanserna att anvndare klickar sig vidare till fretagets egna webbplats, vilket kan leda till nya kunder och strre intkter.
		</p>

		<p>
			Det gr att utforma sin webbplats fr att f bttre placering i skmotorerna, exempelvis genom att ange relevant information och nyckelord, samt genom att se till att andra webbplatser lnkar till sidan. Att <a href="https://sv.wikipedia.org/wiki/S%C3%B6kmotoroptimering" title="Wikipedia: Skoptimering" target="_blank">skoptimera</a> sin webbplats frkortas ofta SEO (Search Engine Optimization) och r ngot som fretag investerar stora pengar i.
		</p>

		<p>
			Serisa webbplatser som anses hlla hg genomsnittlig kvalitet, e.g. Wikipedia, kan komma att prioriteras i resultatlistan, ven om informationen frn annan webbplats innehller mer specifik information kring just din skning.
		</p>

		<info-fields class="infoFields">
			<div class="excercise">

				<div class="hBox">
					TESTA SJLV: GRANSKA RESULTATORDNINGEN
				</div>

				<p>
					Anvnd skfltet nedan och granska resultatordningen.
				</p>

				<search-component query="sevrdheter i lappland"></search-component>
				<search-component query="kan jag cykla p vintern?"></search-component>

				<div class="hBox">
					DISKUTERA
				</div>

				<ul>
					<li>Vilken typ av webbplatser visas bland toppresultaten?</li>
					<li>Hur har avsndarna arbetat med SEO fr att ka spridningen?</li>
					<li>Hur tjnar dessa p att ha en hg placering i resultatlistan?</li>
				</ul>
			</div>

			<div class="excercise">

				<div class="hBox">
					TESTA SJLV: SKOPTIMERING FR OPINIONSBILDNING
				</div>

				<p>
					Gr en subjektiv skning (var kritisk till resultaten!) och resonera kring Googles resultatranking av videoklippet frn den <a href="http://expo.se/2015/poddar---extremhogerns-nya-grepp_6886.html" title="Expo: Poddar - Extremhgerns nya grepp" target="_blank">hgerextrema podden Motgift.nu</a>.
				</p>

				<search-component query="vilket r det bsta statsskicket?"></search-component>

				<div class="hBox">
					DISKUTERA
				</div>

				<ul>
					<li>Vem r avsndaren? (<a href="https://www.iis.se/lar-dig-mer/guider/kallkritik-pa-internet/praktisk-kallkritik-pa-natet/" title="IIS: Kllkritik p ntet" target="_blank">Internetstiftelsen ger praktiska tips</a>).</li>
					<li>Hur har avsndaren arbetat med SEO fr att sprida sitt budskap?</li>
					<li>Resonera kring relationen Google/Youtube, och hur denna relation kan pverka resultatordningen.</li>
				</ul>
			</div>

			<div class="summary">

				<div class="hBox">
					SAMMANFATTNING
				</div>

				Organiska skresultat r sorterade efter relevans, och det gr ej att kpa sig till en bttre placering. SEO innebr att utforma sin webbplats fr att rangordnas hgre av skmotorerna.

			</div>

		</info-fields>

		<h3>Annonser och sponsrade resultat</h3>

    <slide-component>
      <ul>
				<li>Skrddarsydda annonser</li>
				<li>Relevans fr den aktuella skningen</li>
				<li>Kan vara svra att urskilja</li>
			</ul>
    </slide-component>

		<p>
			Du har skert noterat att mnga skningar visar annonser ovanfr resultatlistan. Till skillnad mot de organiska skresultaten kostar denna topplacering pengar och de betalande fretagen har mjlighet att <a href="https://adwords.google.com/home/how-it-works/search-ads/" title="Google: Annonser" target="_blank">skrddarsy sina annonser</a> fr specifika skningar och utifrn sin egen budget.
		</p>

		<p>
			Ju mer specifik en skning r desto strre r chansen att de organiska resultaten kommer att fregs av annonser, srskilt ifall skningen gr att koppla till en specifik produkt. Vilka annonser du ser baseras p din aktuella skning men ven p vilka sidor du tidigare har beskt: har du tidigare beskt en srskild sportbutiks webbplats s kar chanserna att just deras annons visas.
		</p>

		<p>
			Intill betalda annonser s anger skmotorerna ofta orden Annons eller Sponsrad. Trots det kan det ibland vara svrt att skilja annonserna frn de organiska resultaten.
		</p>

		<info-fields class="infoFields">
			<div class="excercise">

				<div class="hBox">
					TESTA SJLV: GRANSKA ANNONSERNA
				</div>

				<p>
					Anvnd skfltet nedan och granska annonserna.
				</p>

				<search-component query="utfrskning"></search-component>
				<search-component query="utfrskning barn"></search-component>
				<search-component query="skidutrustning"></search-component>

				<div class="hBox">
					DISKUTERA
				</div>

				<ul>
					<li>Vilka skningar ger annonser, och vilka r annonsfria?</li>
					<li>r det enkelt eller svrt att skilja annonser frn organiska skresultat?</li>
				</ul>
			</div>

			<div class="summary">

				<div class="hBox">
					SAMMANFATTNING
				</div>

				Fretag kan kpa annonsplatser som skrddarsys mot specifika skningar eller anvndarprofiler. Annonserna visas ofta ovanfr eller intill resultatlistan, men kan ibland vara svra att urskilja frn de organiska resultaten.

			</div>

		</info-fields>

		<h3>Att anpassa sin egen skmotor</h3>

    <slide-component>
      <ul>
				<li>Anpassa och anvnd Googles skmotor p egen webbsida</li>
				<li>Stll in parametrar: pverka skresultaten</li>
				<li>Inkluderade webbplatser, sprk, extra skord, m.m.</li>
			</ul>
    </slide-component>

		<p>
			Det r mjligt att, med Googles teknik, skapa egna <a href="https://cse.google.com" title="Google CSE" target="_blank">anpassade skmotorer</a> att anvnda p sin egen webbsida. Det vanligaste anvndningsomrdet r att dra nytta av Googles indexering och kraft fr att erbjuda beskaren skning inom den egna webbplatsen, se exempel p <a href="http://www.londontown.se/" title="Londontown" target="_blank">Londontown</a> med en skning p <i>museum</i>.
    <p>

    <p>
      Utver skning p den egna webbplatsen erbjuds webbutvecklaren mjligheten att styra en mngd parametrar fr svl skning som presentation av skresultaten. Det r p s stt mjligt att pverka skresultaten, ven utan att anvndaren r medveten om det.
		</p>

		<p>
			Exempel p styrbara parametrar:
			<ul>
				<li>Vilka webbplatser som ska inkluderas/exkluderas.</li>
				<li>Vilka sprk som ska inkluderas i skresultaten.</li>
				<li>Extra skord eller mnen, som inte syns fr anvndaren.</li>
				<li>Prioriterade annonser.</li>
			</ul>

		</p>

		<info-fields class="infoFields">

			<div class="excercise">

				<div class="hBox">
					TESTA SJLV: ANPASSADE SKMOTORER
				</div>

				<p>
					Nedan r tv anpassade skmotorer frn Google som r skapade fr denna webbplats. Bda prioriterar, eller visar uteslutande, resultat frn <a href="https://sv.wikipedia.org/wiki/Lista_%C3%B6ver_svenska_dagstidningar" title="Wikipedia: Lista ver svenska dagstidningar" target="_blank">rikstckande svenska dagstidningarna listade p Wikipedia</a>. Fr anvndaren ser de bda skflten identiska ut, och intrycket r att de bda kommer att ge "vanliga resultat" frn Google.
				</p>

				<p>
					Gr samma skning i bda skflten nedan, frslagsvis med namnet p valfri svensk ort. Jmfr resultaten som visas.
				</p>

        <search-custom cx="010667227527707256572:priyvcvqxnk" button-text="Sk p Google"></search-custom>
        <search-custom cx="010667227527707256572:ldvhe02mbqi" button-text="Sk p Google"></search-custom>

				<div class="hBox">
					VAD HNDE HR?
				</div>

				<p>
					Samtliga resultat r frn Google och lnkar till riktiga sidor och nyhetsartiklar. Trots detta kan de bda skflten ge avsevrt olika resultat. Medan det vre skfltet ger "vanliga resultat", sker det undre skfltet inom mnena <i>vld, knark, brott,</i> och <i>extremism</i>, utan att du som anvndare r medveten om det.
				</p>

				<div class="hBox">
					DISKUTERA
				</div>

				<ul>
					<li>Hur kan anpassade skmotorer nyttjas fr att hjlpa anvndaren att hitta det hen sker?</li>
					<li>Hur kan anpassade skmotorer missbrukas fr att vilseleda anvndaren?</li>
				</ul>

			</div>

			<div class="summary">

				<div class="hBox">
					SAMMANFATTNING
				</div>

				Det gr att anpassa och anvnda Googles skmotor p sin egen webbplats. Tekniken kan bde nyttjas och missbrukas fr att styra skresultaten i en srskild riktning.

			</div>

		</info-fields>

  </div>
</template>

<script>

import InfoFields from '@/components/learn/InfoFields.vue'
import MediaContainer from '@/components/learn/MediaContainer.vue'
import SearchComponent from '@/components/learn/SearchComponent.vue'
import SearchCustom from '../SearchCustom.vue'
import SlideComponent from '@/components/learn/SlideComponent.vue'

export default {
  components: {
    InfoFields,
    MediaContainer,
    SearchComponent,
    SearchCustom,
    SlideComponent
  },
  data () {
    return {
      'header': 'Bland skresultat och annonser'
    }
  }
}
</script>

