<template>
  <div>

    <p>
      En skmotor frsker frst vad det r du faktiskt letar efter. Deras algoritmer r bland annat trnade till att avgra vilka ord i din skning som r de viktiga, s kallade nyckelord.
    </p>

    <p>
      Ifall du endast sker p ett enda ord, t.ex. <i>fisk</i>, s har skmotorn svrt att avgra exakt vad du letar efter, medan en hel skmening <i>kan jag ta fisk frn stersjn</i> hjlper skmotorn att frst vad du r ute efter.
    </p>

    <p>
      I fljande skterm r nyckelorden markerade med fet stil:
    </p>

    <p>
      <i>
        kan jag <strong>ta</strong> <strong>fisk</strong> frn <strong>stersjn</strong>
      </i>
    </p>



    <info-fields class="infoFields">

			<div class="excercise">

				<div class="hBox">
          TESTA SJLV: SK MED NYCKELORD
				</div>

