				</p>

				<search-component query="kan jag ta fisk frn stersjn"></search-component>
        <search-component query="kan jag fiska i stersjn"></search-component>
        <search-component query="ta fisk stersjn"></search-component>
        <search-component query="fiskar i stersjn"></search-component>

				<div class="hBox">
					DISKUTERA
				</div>

				<ul>
					<li>Vilka r nyckelorden i respektive skning?</li>
					<li>Hur pverkar valet av nyckelord resultaten?</li>
				</ul>

			</div>

			<!--div class="summary">

				<div class="hBox">
					SAMMANFATTNING
				</div>

        Anvnd nyckelord fr att gra dina skningar mer noggranna.

			</div-->

			<!--div class="more">

				<div class="hBox">
					FRDJUPNING
				</div>

				Mikoteket: <router-link to="/digitalkompetens/sok/3">Skteknik och skkritik: Bli expert p att ska</router-link>
			</div-->

		</info-fields>

  </div>
</template>

<script>

import InfoFields from '@/components/learn/InfoFields.vue'
import SearchComponent from '@/components/learn/SearchComponent.vue'

export default {
  name: 'SearchKeywords',
  components: {
    InfoFields,
    SearchComponent,
  },
  data () {
    return {
      'name': 'Sk med nyckelord',
      'route': 'nyckelord'
    }
  }
}
</script>

