  <h3>Frmedla och mjliggra funktionalitet</h3>

  <p>
    Bra design ska, utver att vara estetiskt tilltalande, frmedla eller synliggra objektets funktion: <i>vad</i> anvnds objektet till, och <i>hur</i> anvnds det? Vice versa ska objektet vara designat s att den avsedda och frvntade funktionaliteten mjliggrs.
  </p>

  <p>
     Vidare br all design utg frn den tnkta anvndaren och dennes behov. Hnsyn ska tas till anvndarens frkunskaper, frutsttningar och begrnsingar, den tid och situation d objektet r tnkt att anvndas, samt vrigt som pverkar hur objektet eller anvndandet av detsamma upplevs av den tilltnkte anvndaren.
  </p>

  <p>
    Mnga av vra vardagliga freml har en vl genomarbetad design som vi ofta tar fr given och inte funderar srskilt mycket kring. Fr att synliggra designen i vardagsfreml skapar konstnren Katerina Kamprani i projektet <a href="https://www.theuncomfortable.com" target="_blank">The Uncomfortable</a> objekt i vilka den obekvma designen motverkar, snarare n stdjer, den funktionalitet anvndaren frvntar sig eller r van vid.
  </p>

  <p>
    Bra design ska vara tillgnglig fr alla. ven om vra vardagliga freml ofta har en genomtnkt design kan de i mnga fall vara svra eller rentav farliga att anvnda fr personer med olika funktionsnedsttningar. I videon nedan presenteras en designer i Singapore som, efter att ha noterat sr och rr p mnga blinda personers hnder och armar till fljd av matlagning med kksverktyg utformade fr seende personer, brjade omdesigna vanliga freml i kket.
  </p>

