  <h3>Webbriktlinjer och tillgnglighet</h3>

  <p>
    Nr man designar och utvecklar webbplatser finns det internationellt framtagna <a href="https://www.w3.org/standards/" target="_blank">riktlinjer och standarder</a> att flja. P <a href="https://webbriktlinjer.se/" target="_blank">Webbriktlinjer.se</a> anges de officiella riktlinjerna fr hur svensk offentlig sektor br arbeta med webbplatser, samt <a href="https://webbriktlinjer.se/lagkrav/webbdirektivet/" target="_blank">EU:s webbdirektiv</a> som r lag frn 2019. I avdelningen <a href="https://webbriktlinjer.se/webbforalla/" target="_blank">Webb fr alla</a> presenteras srskilt information om webbutveckling ur ett tillgnglighetsperspektiv.
  </p>

  <p>
    ven om riktlinjerna r framtagna fr verktyg och tjnster p webben r de i mnga fall applicerbara ven p andra skrmbaserade verktyg, ssom mobilappar, spel, etc. En grundprincip r att anvndarens kognitiva belastning ska minimeras. Information ska vara enkel att hitta och anvndaren ska knna att hen r i kontroll.
  </p>

  <p>
    Nedan fljer ett par grundlggande designprinciper hrledda frn webbriktlinjerna:
    <ul class="nested">
      <li>
        Information ska vara tydligt strukturerad.
        <ul>
          <li>
            Det ska vara enkelt att skilja p webbplatsens olika delar.
          </li>
          <li>
            Information ska presenteras i en logisk och konsekvent fljd.
          </li>
          <li>
            Alternativa texter ska finnas fr bilder, videor, lnkar, etc.
          </li>
          <li>
            verfldig information och intryck ska undvikas.
          </li>
        </ul>
      </li>
      <li>
        Textavsnitt ska vara lsbara.
        <ul>
          <li>
            Typsnitt ska vara tydliga och i lagom textstorlek.
          </li>
          <li>
            Sprket ska vara anpassat till anvndaren.
          </li>
          <li>
            Frger ska anvndas genomtnkt och ge bra kontraster.
          </li>
        </ul>
      </li>
      <li>
        Designa fr mobila och sm skrmar
        <ul>
            <li>
              Layouten ska anpassas automatiskt till olika skrmstorlekar.
            </li>
            <li>
              Knappar och andra interaktiva element ska ha tillrckligt stor klick- och touchyta.
            </li>
            <li>
              Interaktiva element ska inte ligga fr ttt.
            </li>
        </ul>
      </li>
      <li>
        Synliggr funktionaliteten.
        <ul>
          <li>
            <a>Lnkar</a>, <button>knappar</button>, etc. ska vara tydligt markerade.
          </li>

          <li>
            Effekten av en interaktion ska vara tydlig, anvndaren ska inte behva gissa eller testa vad som kommer att hnda.
          </li>
        </ul>
      </li>
      <li>
        Ge terkoppling vid interaktion.
        <ul>
          <li>
            Effekten av en interaktion ska ske <i>nr</i> och <i>dr</i> anvndaren interagerar, inte med frdrjning eller p ngon annan del av skrmen.
          </li>
          <li>
            Vid behov, visa vad som pgr, exempelvis vid laddning av data.
          </li>
          <li>
            Undvik mjligheten fr anvndaren att gra fel eller ge tydliga felmeddelanden.
          </li>

        </ul>
      </li>
      <li>
        Navigation
        <ul>
          <li>
            Det ska g att navigera med tangentbordet, och se vad som r markerat. Exempel: <button>Klicka p mig</button> <button>Tryck p tab-tangenten</button> <button>Tryck p tab-tangenten igen</button> <button>Och igen...</button>.
          </li>
          <li>
            Erbjud mjligheten att avbryta samt att g tillbaka.
          </li>
        </ul>
      </li>
    </ul>
  </p>

  <p>
    Vl strukturerad information enligt standarder r en frutsttning fr mnga tillgnglighetsverktyg att fungera. Nstan <a href="https://internet.begripsam.se/#/stats/Anva%25CC%2588ndning%2520av%2520internet%2520SMFOI%25202017/1052" target="_blank">tre av fyra blinda anvndare tycker det r svrt att anvnda internet</a>, och varannan anger som skl att webbplatser inte fungerar med deras hjlpmedel. Ungefr var femte person med funktionsnedsttning och mer n var tredje person med lssvrigheter upplever att det r svrt att navigera/hitta till rtt stlle p internet.
  </p>

  <p>
    Fr kad tillgnglighet br en webbplats g att anpassa efter egna behov, och erbjuda mjligheten att:
    <ul>
      <li>
        ndra textstorleken.
      </li>
      <li>
        Zooma.
      </li>
      <li>
        ndra frger.
      </li>
      <li>
        Dlja strande eller verfldiga element.
      </li>
      <li>
        Navigera och hitta information p fler n ett stt.
      </li>
      <li>
        Skapa direktlnkar till information.
      </li>
    </ul>
  </p>

  <p>
    Utvecklare av webbplatser kan bygga in frdigt std fr tillgnglighetsanpassning, till exempel genom tjnsten <a href="https://www.funka.com/vi-erbjuder/talande-webb/" target="_blank">Talande Webb</a>, som inte krver att anvndaren sjlv installerar ndvndiga verktyg. Detta r srskilt viktigt vid anvndning av exempelvis publika datorer eller i system dr anvndaren inte sjlv har behrighet eller mjlighet att installera nya program.
  </p>

  <p>
    Mikotekets webbplats erbjuder ett antal <router-link :to="{ name: 'Accessibility'}">inbyggda tillgnglighetsverktyg</router-link> som inte frutstter att anvndaren installerar nya program.
  </p>

  <media-container class="mediaContainer">
    <div>
      <div class="content">
        <iframe src="https://player.vimeo.com/video/123300019" width="100%" height="360" frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>
