        </li>
        <li>
          Hur mnga gnger har du klickat p knappen? <span @click="showClick = true" v-if="!showClick" class="showClick">Klicka hr fr att se svar.</span><span v-if="showClick"> Du har klickat {{clickCounter}} gnger.</span>
        </li>
        <li>
          Vilken knsla fr du som anvndare nr systemet inte beter sig som frvntat?
        </li>
