      </div>

      <ul>
        <li>
          Sammanstllning och exemplifiering av de viktigaste webbriktlinjerna <a href="https://webbriktlinjer.se/wcag/" target="_blank">WCAG  standard fr tillgnglighet</a>, samt sammanfattning av <a href="https://webbriktlinjer.se/lagkrav/webbdirektivet/" target="_blank">EU:s webbdirektiv</a>.
        </li>
        <li>
          Svensk versttning av <a href="http://www.certec.lth.se/fileadmin/certec/publikationer/De_7_Principerna_foer_Universell_Design.pdf" target="_blank">Principerna fr Universell Design</a>.
        </li>
        <li>
          <a-wiki c="Jakob_Nielsen_(datalog)#Nielsens_tio_heuristiska_principer[1]">Nielsens tio heuristiska principer</a-wiki> fr grnssnittsdesign.
        </li>
        <li>
          Ta del av <a href="https://www.funka.com/design-for-alla/tips-och-rad/" target="_blank">tips och rd p Funkas webbplats</a>.
        </li>
      </ul>

    </div>

  </info-fields>

  <h3>Design fr alla</h3>

  <p>
    Begreppen <a-wiki c="Design_fr_alla">Design fr Alla</a-wiki> och <a-wiki c="Universell_utformning">Universell design</a-wiki> innebr att produkter och tjnster ska utformas fr att kunna tillgodoses av s mnga som mjligt utan behov av speciella anpassningar. En grunddesign som r tillgnglig fr personer med srskilda behov innebr ofta frdelar fr alla personer, oavsett funktionsfrmga, lder eller tidigare erfarenhet.
  </p>

  <p>
    Enligt Webbriktlinjer har cirka <a href="https://webbriktlinjer.se/webbforalla/" target="_blank">en av fem personer en funktionsnedsttning</a>, och nnu fler har det periodvis i livet. Man brukar skilja p <span class="semibold">permanenta</span> (blindhet, dvhet, saknar en hand, etc.), <span class="semibold">temporra</span> (starr, roninfektion, gipsad arm) och <span class="semibold">situationsberoende</span> (blndning, bullriga miljer, br ett barn) nedsttningar. Genom att designa fr ytterligheterna, exempelvis fr personer med permanenta nedsttningar, s skapar man ofta ven en tillgnglig design fr mnga fler - ven fr personer utan nedsttning.
  </p>

  <p>
    I projektet <a href="https://www.funka.com/projekt/myter-om-tillganglighet/" target="_blank">Myter om tillgnglighet</a> tar sig <a href="https://www.funka.com/" target="_blank">Funka</a> an myten att tillgngliga webbplatser behver vara svart-vita och trkiga, och presenterar flera exempel p <a href="https://www.funka.com/projekt/myter-om-tillganglighet/kan-tillgangliga-webbplatser-vara-fargglada/" target="_blank">webbplatser som r svl tillgngliga som estetiskt tilltalande</a>.
  </p>

  <p>
    Kort och gott tjnar alla p kad tillgnglighet!
  </p>

