<template>
  <div>

    <slide-component>
      <ul>
        <li>Stora mjligheter att skapa & sprida eget innehll</li>
      </ul>
    </slide-component>

    <p>
      Ngot som utmrker information p internet r de stora mjligheterna att kopiera och sprida digitalt innehll. Detta r srskilt intressant fr sociala medier, i vilken informationen hittar <i>dig</i> utan att du aktivt eller medvetetet sker upp den.
    </p>

    <h3>Automatisk spridning genom algoritmer</h3>

    <slide-component>
      <ul>
        <li>Algoritmer styr ett inlggs rckvidd</li>
        <li>Olika typer av engagemang kar ett inlggs popularitet</li>
        <li>Populra inlgg fr strre spridning</li>
      </ul>
    </slide-component>

    <p>
      Nr en anvndare publicerar innehll i exempelvis sociala medier s r det respektive plattforms <a-wiki c="algoritm">algoritmer</a-wiki> som avgr vem som kommer att se inlgget. Det kan ofta vara svrt att gra sin rst hrd i informationsbruset. Nya inlgg visas frmst fr den nrmsta kretsen medan populra inlgg har potential att n en betydligt strre publik.
    </p>

    <p>
      Ett stt fr plattformarna att bedma ett inlggs popularitet r att mta till vilken grad andra anvndare interagerar med innehllet. ven om mjligheterna till engagemang skiljer sig frn plattform till plattform, s r det vanligt r att lta anvndare:
    </p>

    <ul>
      <li>
        <span class="semibold">Klicka</span>: klicka p ett inlgg, e.g. fr att lsa mer, frstora en bild eller beska en lnkad webbplats.
      </li>
      <li>
        <span class="semibold">Gilla/Reagera</span>: ge snabb, positiv feedback p inlgg, e.g. med en tumme upp eller hjrta. Facebook erbjuder, sedan 2016, ytterligare reaktioner: skratt, arg, ledsen och krlek.
      </li>
      <li>
        <span class="semibold">Kommentera</span>: svara p ett befintligt inlgg eller en annan anvndares kommentar.
      </li>
      <li>
        <span class="semibold">Dela</span>: aktivt dela vidare en annan anvndares inlgg.
      </li>
      <li>
        <span class="semibold">Flja</span>: prenumerera p en annan anvndares inlgg.
      </li>
    </ul>

    <p>
      Samtliga interaktioner gr att ett inlgg bedms som mer populrt och spridningen till andra anvndare kar. Utver att lta anvndare aktivt interagera med inlgg s har plattformarna ven mjlighet att bedma popularitet utifrn passiva handlingar, e.g. hur lnge en anvndare tittar p ett inlgg innan hen scrollar vidare i fldet.
    </p>

    <info-fields class="infoFields">

      <div class="summary">

  			<div class="hBox">
  				SAMMANFATTNING
  			</div>

  			Inlgg som skapar engagemang p sociala medier bedms av algoritmerna som populra. Dessa visas automatiskt fr fler anvndare och fr drmed strre spridning.

  		</div>

  	</info-fields>

    <h3>Personligt anpassat innehll</h3>

    <slide-component>
      <ul>
        <li>Algoritmer analyser ditt surfbeteende</li>
        <li>Bedmer och visar innehll intressant fr just dig</li>
        <li>Ekokammare & filterbubblor</li>
        <li>Var kritisk till urvalet</li>
      </ul>
    </slide-component>

    <p>
      Nr du surfar p sociala medier s ser du endast en minimal brkdel av allt material som publiceras, berknat till omkring en halv miljon Snapchatbilder, nstan lika mnga Twitterinlgg, och nra 50.000 Instagrambilder, <a href="https://www.domo.com/learn/data-never-sleeps-5" title="Data Never Sleeps 2.0
" target="_blank">varje minut</a>. ven av det material som dina vnner publicerar, kommer majoriteten aldrig att visas fr dig.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="image">
          <a href="https://www.domo.com/learn/data-never-sleeps-5" title="Data Never Sleeps 2.0
" target="_blank" class="nohover">
            <img src="https://web-assets.domo.com/blog/wp-content/uploads/2017/07/17_domo_data-never-sleeps-5-01.png"
           alt="infographic" />
         </a>
        </div>
        <div class="caption">
          S mycket material publicerades p olika sociala plattformar, varje minut under 2017.
        </div>
        <div class="source">
          Klla: <a href="https://www.domo.com/learn/data-never-sleeps-5" title="Data Never Sleeps 2.0
" target="_blank">Data Never Sleeps 5.0, Domo</a>
        </div>
      </div>
    </media-container>

    <p>
      Fr att behlla dig som anvndare r det viktigt att plattformen erbjuder en bra upplevelse. En viktig del i detta r att presentera sdant innehll som bedms vara intressant fr just dig. P samma stt som plattformarnas algoritmer bedmer ett inlggs popularitet s arbetar dessa ven fr att skapa en frstelse fr vem du r och vad du gillar.
    </p>

    <p>
      Innehll som bedms som relevant fr dig kommer att visas, medan andra inlgg aldrig kommer att n dig. Klickar du ofta p material som delas av en specifik vn s kommer du att se mer av hens inlgg, samt saker som hen har interagerat med. Reagerar du ofta p inlgg frn politiska Facebooksidor kommer du att presenteras fr mer liknande politiskt innehll. Slutar du scrolla nr det kommer en video med <a href="https://www.youtube.com/watch?v=we9_CdNPuJg" title="Fainting goats" target="_blank">svimmande getter </a> s kommer du att f se fler sta djurklipp.
    </p>

    <p>
      Kort och gott: istllet fr att du aktivt sker upp innehllet, ssom nr du anvnder en skmotor, s hittar innehllet i sociala medier dig. Du fr helt enkelt ett personligt flde.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="content">
          <iframe src="https://www.facebook.com/plugins/video.php?href=https%3A%2F%2Fwww.facebook.com%2Fsvt%2Fvideos%2F1278934345482484%2F&show_text=0&width=560" width="560" height="315" style="border:none;overflow:hidden" scrolling="no" frameborder="0" allowTransparency="true" allowFullScreen="true"></iframe>
        </div>
        <div class="caption">
          SVT:s Sociala Monster illustrerar hur sociala mediers algoritmer arbetar fr att ge oss personliga flden.
        </div>
        <div class="source">
          Klla: <a href="https://www.facebook.com/svt/videos/1278934345482484/" title="Filterbubblan p tv minuter" target="_blank">SVT (Facebook)</a>
        </div>
      </div>
    </media-container>

    <p>
      I en milj dr allt material r srskilt utvalt fr dig r det vrt att fundera:

      <ul>
        <li>
          Varfr presenteras <i>just den hr informationen</i> fr mig?
        </li>
        <li>
          Vad fr jag <i>inte</i> se, och vad fr andra personer se?
        </li>
      </ul>

    </p>

    <p>
      De <a href="https://fof.se/artikel/forskare-filterbubblan-ar-en-myt" title="Forskare sticker hl p myten om filterbubblor" target="_blank">omstridda</a> begreppen <a-wiki c="Filterbubbla">filterbubbla</a-wiki> och <a-wiki c="Ekokammare">ekokammare</a-wiki> beskriver hur vi p ntet, medvetet eller omedvetet som en fljd av ntets algoritmer, tenderar att sluta in oss i informationsbubblor med ensidiga perspektiv. En konsekvens av detta r att vi isolerar oss frn sikter vi inte delar och som utmanar vr vrldsbild.
    </p>

    <p>
      Oavsett filterbubblornas faktiska inverkan kvarstr det faktum att ditt flde p sociala medier r skrddarsytt fr att passa just dig. terigen r det vrt att fundera:

      <ul>
        <li>
          Hur nyanserade r mina sociala flden?
        </li>
        <li>
          Hur pverkas jag av innehllet?
        </li>
        <li>
          Hur pverkas andra personer av <i>sitt</i> personliga flde?
        </li>
      </ul>

    </p>

    <p>

      Srskilt nr du presenteras fr sikter som du <i>inte</i> delar kan du resonera kring:

      <ul>
        <li>
          Varfr fr jag se <i>just detta inlgg</i>?
        </li>
        <li>
          Har inlgget en informerande eller en provocerande ton?
        </li>
        <li>
          Hur har andra reagerat p inlgget? r folk glada eller arga?
        </li>
        <li>
          r detta inlgg representativt, eller r det utvalt fr att det har skapat mnga reaktioner?
        </li>
        <li>
          Finns det mer <i>nyanserade</i> perspektiv som jag inte presenteras fr?
        </li>
      </ul>
    </p>

    <info-fields class="infoFields">

      <div class="excercise">

        <div class="hBox">
          TESTA SJLV: ANALYSERA DITT FLDE
        </div>

        <p>
          Besk de sidor eller sociala plattformar dr du tar del av nyheter. Bilda dig en verblick av de nyheter som presenteras, genom att lsa rubriker och eventuella ingresser.
        </p>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Vilken typ av nyheter dyker upp i ditt flde?
          </li>
          <li>
            Tar du del av nyheter frn flera kllor? r dessa oberoende eller presenterar de liknande perspektiv?
          </li>
          <li>
            Tar du ofta aktivt del av ett flera perspektiv kring en nyhet?
          </li>
        </ul>

      </div>

      <div class="excercise">

        <div class="hBox">
          TESTA SJLV: SYNLIGGR ALGORITMERNA
        </div>

        <p>
          Besk en social plattform som du r med i. Vlj ut ett par inlgg som du tycker r extra intressanta.
        </p>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Diskutera utifrn frgestllningarna i kapiteltexten ovan.
          </li>
          <li>
            Hur har algoritmerna arbetat fr att vlja ut informationen t mig?
          </li>
        </ul>

      </div>

      <div class="summary">

  			<div class="hBox">
  				SAMMANFATTNING
  			</div>

        <p>
          Utifrn hur du anvnder sociala medier skapar sig respektive plattforms algoritmer en frstelse fr vem du r och vad du gillar. Denna kunskap anvnds fr att presentera utvalda inlgg som bedms som intressanta fr just dig. Dina vnner har egna personliga flden med annat innehll n vad du ser.
        </p>

  		</div>

    </info-fields>

    <h3>Virala inlgg</h3>

    <slide-component>
      <ul>
        <li>Oproportionerligt stor spridning</li>
        <li>Spelar p mottagarens knslor</li>
        <li>Delas oreflekterat vidare</li>
      </ul>
    </slide-component>

    <p>
      Ibland fr inlgg eller nyheter s pass stor spridning i sociala medier och andra forum att de dyker upp i var och varannan persons nyhetsflde. Sdana inlgg, som framfrallt sprids i sociala ntverk och som ofta fr oproportionerligt stor spridning i frhllande till sitt nyhetsvrde, kallas fr virala. Det kan handla om enskilda inlgg som skapar stort engagemang, svl som nyhetshndelser som genererar en stor mngd inlgg frn olika hll.
    </p>

    <p>
      Det r inte ovanligt att inlgg som fr stor spridning spelar p folks knslor. Innehll som fr dig att knna r mer troliga att du kommer gilla, reagera p, kommentera eller dela vidare. Den spontana knslan kan vara s stark att du i all vlmening delar ett inlgg, video eller artikel vidare, utan att ha reflekterat ver - eller ens tagit del av hela - innehllet. Inte sllan handlar det om innehll som fr dig som lsare att knna rdsla eller bli upprrd.
    </p>

    <p>
      I videon nedan illustrerar <a href="https://www.metro.se/viralgranskaren" title="Viralgranskaren" target="_blank">Metros Viralgranskaren</a> och <a href="https://www.iis.se/" title="Internetstiftelsen i Sverige" target="_blank">Internetstiftelsen i Sverige</a> hur en falsk nyhet hsten 2016 snabbt blev viral och skapade ilska i sociala medier. I videon jmfrs hur personer <i>med</i> respektive <i>utan</i> kllkritisk blick kan tnkas ha reagerat p bluffen.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="content">
          <iframe width="560" height="315" src="https://www.youtube.com/embed/wc353OW5kW0" frameborder="0" allowfullscreen></iframe>
        </div>
        <div class="caption">
          Viralgranskaren och Internetstiftelsen jmfr hur tv olika personer hanterar en viral nyhet, med respektive utan ett kllkritisk tnkande.
        </div>
        <div class="source">
          Klla: <a href="https://www.youtube.com/watch?v=wc353OW5kW0" title="Youtube: Kllkritik p ntet r viktigare n ngonsin" target="_blank">MetroSverige (Youtube)</a>
        </div>
      </div>
    </media-container>

    <info-fields class="infoFields">

      <div class="excercise">

        <div class="hBox">
          TESTA SJLV: VIRALA NYHETER
        </div>

        <p>
          Besk <a href="https://www.metro.se/om/%C3%A5rslista" title="Viralgranskaren: rslistor" target="_blank">Viralgranskarens rslistor</a> fr att lsa mer om de senaste rens granskningar och trender. Klicka vidare p valfritt rtal fr att lista rets samtliga granskningar.
        </p>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Knner du igen ngon eller ngra av de virala nyheterna?
          </li>
          <li>
            Har du sjlv klickat, spridit och/eller trott p ngot av de falska pstenden som har spridits?
          </li>
          <li>
            Hur agerar du nr en viral nyhet som du sjlv har spridit visar sig vara falsk?
          </li>
        </ul>

      </div>

      <div class="summary">

  			<div class="hBox">
  				SAMMANFATTNING
  			</div>

        <p>
          Virala inlgg och nyheter r sdana som fr oproportionerligt stor spridning i sociala medier. Dessa spelar ofta p lsarens knslor som delar inlgget vidare utan att kritiskt reflektera ver innehllet.
        </p>

  		</div>


      <div class="more">

        <div class="hBox">
          FRDJUPNING
        </div>

        <p>
          Metros <a href="https://www.metro.se/viralgranskaren" title="Viralgranskaren" target="_blank">Viralgranskaren</a> granskar sedan 2014 nyheter och pstenden som har blivit virala i sociala medier. Hos <a href="https://www.metro.se/lilla-viralgranskaren" title="Lilla Viralgranskaren" target="_blank">Lilla Viralgranskaren</a> fr barn 6-18 r hittar du skolmaterial och interaktiva uppgifter kring virala nyheter.
        </p>

      </div>

    </info-fields>

  </div>
</template>

<script>

import AWiki from '@/components/AWiki.vue'
import InfoFields from '@/components/learn/InfoFields.vue'
import MediaContainer from '@/components/learn/MediaContainer.vue'
import SoiGraph from '@/components/SoiGraph.vue'
import SlideComponent from '@/components/learn/SlideComponent.vue'

export default {
  components: {
    AWiki,
    InfoFields,
    MediaContainer,
    SoiGraph,
    SlideComponent
  },
  data () {
    return {
      'header': 'Spridning och virala inlgg'
    }
  },
  created () {

  }
}
</script>

<style>
</style>



